}
.header .bgc {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .fade-enter-active,
.header .fade-leave-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.header .fade-enter,
.header .fade-leave-active {
  opacity: 0;
}
