
.fooddetail {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.fooddetail.flag-enter,
.fooddetail.flag-leave {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.fooddetail .image-loader {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.fooddetail .image-loader img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.fooddetail .image-loader .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.fooddetail .image-loader .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.fooddetail .content {
  position: relative;
  padding: 18px;
}
.fooddetail .content .title {
  line-height: 14px;
  font-size: 14px;
  margin-bottom: 8px;
  font-weight: 700;
  color: #07111b;
}
.fooddetail .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
}
.fooddetail .content .detail .sell-count,
.fooddetail .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.fooddetail .content .detail .sell-count {
  margin-right: 12px;
}
.fooddetail .content .price {
  font-weight: 700;
  line-height: 24px;
}
.fooddetail .content .price .now {
  color: #f01414;
  font-size: 14px;
  margin-top: 8px;
}
.fooddetail .content .price .old {
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.fooddetail .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.fooddetail .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
}
.fooddetail .info {
  padding: 18px;
}
.fooddetail .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #4d555d;
}
.fooddetail .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.fooddetail .rating {
  padding-top: 18px;
}
.fooddetail .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #4d555d;
}
.fooddetail .rating .rating-wrapper {
  padding: 0 18px;
}
.fooddetail .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.fooddetail .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.fooddetail .rating .rating-wrapper .rating-item .user {
  position: absolute;
  top: 16px;
  right: 0;
  line-height: 12px;
  font-size: 0;
}
.fooddetail .rating .rating-wrapper .rating-item .name {
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  font-size: 10px;
  color: #93999f;
}
.fooddetail .rating .rating-wrapper .rating-item .avatar {
  border-radius: 50%;
}
.fooddetail .rating .rating-wrapper .rating-item .time {
  margin-bottm: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #07111b;
}
.fooddetail .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.fooddetail .rating .rating-wrapper .rating-item .text .icon-thumb_down,
.fooddetail .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  margin-right: 4px;
  line-height: 24px;
  font-size: 12px;
}
.fooddetail .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.fooddetail .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.fooddetail .rating .rating-wrapper .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


