  position: relative;
}
.sellers .sellers-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.sellers .sellers-wrapper .title {
  font-size: 14px;
