  margin-right: 8px;
}
.sellers .sellers-wrapper .star-wrapper .text {
  display: inline-block;
  vertical-align: top;
  margin-right: 12px;
  font-size: 10px;
  line-height: 18px;
  color: #4d555d;
}
.sellers .overview {
  display: -webkit-box;
  display: -ms-flexbox;
